#footer-menu-container {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 0 30px;
}
@media (max-width: 1100px) {
  #footer-menu-container {
    flex-direction: column;
    align-items: flex-end;
  }
}
@media (max-width: 800px) {
  #footer-menu-container {
    align-items: center;
  }
}
#footer-menu-container #links-container {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  width: 100%;
  padding: 20px;
}
@media (max-width: 1100px) {
  #footer-menu-container #links-container {
    justify-content: flex-start;
    width: 80%;
  }
}
@media (max-width: 800px) {
  #footer-menu-container #links-container {
    justify-content: center;
    width: 100%;
  }
}
@media (max-width: 500px) {
  #footer-menu-container #links-container {
    flex-direction: column;
  }
}
#footer-menu-container #links-container .links {
  display: flex;
  flex-direction: column;
  margin: 0 60px;
  min-width: 100px;
}
#footer-menu-container #links-container .links li {
  list-style: none;
  margin: 5px 0;
}
#footer-menu-container #links-container .links a {
  color: #000000;
  text-decoration: none;
  list-style: none;
  font-weight: 300;
}
#footer-menu-container #links-container .links a:hover {
  text-decoration: underline;
}
#footer-menu-container #links-container .links .links-header {
  color: #CC9831;
  font-weight: bold;
}
#footer-menu-container #links-container :first-child {
  margin-left: 0;
}
#footer-menu-container #policy-social {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  min-width: 300px;
  padding: 20px;
}
@media (max-width: 1100px) {
  #footer-menu-container #policy-social {
    align-items: flex-start;
    width: 80%;
  }
}
@media (max-width: 800px) {
  #footer-menu-container #policy-social {
    align-items: center;
    width: 100%;
  }
}
#footer-menu-container #policy-social #social-container {
  display: flex;
  flex-direction: row;
  padding-bottom: 10px;
}
#footer-menu-container #policy-social #social-container .social-icon {
  border: 2px solid #000;
  margin: 5px;
  width: 30px;
  height: 30px;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-content: center;
}
#footer-menu-container #policy-social #social-container .social-icon:hover {
  background-color: #CC9831;
  border-color: #000;
}
#footer-menu-container #policy-social #social-container .social-icon a {
  display: flex;
  justify-content: center;
  align-items: center;
}
#footer-menu-container #policy-social #social-container .social-icon a img {
  width: 90%;
  height: auto;
}
#footer-menu-container #policy-social hr {
  border: 1px solid #ccc;
  width: 50%;
  margin: 0;
}
@media (max-width: 1100px) {
  #footer-menu-container #policy-social hr {
    width: 100%;
  }
}
#footer-menu-container #policy-social #copyright {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  font-weight: 300;
}
@media (max-width: 1100px) {
  #footer-menu-container #policy-social #copyright {
    align-items: unset;
  }
}
#footer-menu-container #policy-social #copyright p, #footer-menu-container #policy-social #copyright #pipe {
  color: #000000;
}
#footer-menu-container #policy-social #copyright div :first-child {
  padding-right: 5px;
}
#footer-menu-container #policy-social #copyright div :last-child {
  padding-left: 5px;
}
#footer-menu-container #policy-social #copyright a {
  color: #CC9831;
  text-decoration: none;
}
#footer-menu-container #policy-social #copyright a:hover {
  text-decoration: underline;
}

